.bodyBg::after {
    content: '';
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-image: url('../../../src/images/bg-mobile.png');
    background-size: cover;
  }
  
  @media screen and (min-width: 768px) {
    .bodyBg::after {
      background-image: url('../../../src/images/bg-tablet.png');
    }
  }
  
  @media screen and (min-width: 1280px) {
    .bodyBg::after {
      background-image: url('../../../src/images/bg.png');
    }
  }
  